.v-news {
  padding: 0;
  margin: 0;
  list-style: none;
}
.v-news__item {
  border-top: 1px solid #c8c8c8;
}
.v-news__item:last-of-type {
  border-bottom: 1px solid #c8c8c8;
}
.v-news__item > a {
  display: flex;
  align-items: center;
  padding: 17px 14px;
  color: #24292e;
  text-decoration: none;
}
.v-news__item-time {
  margin-right: 76px;
}
.v-news__item-title {
  flex: 1;
  margin: 0;
}