html {
  font-size: 62.5%;
  font-family: "Noto Sans JP", sans-serif;
}

body {
  font-size: 1.44rem;
  color: #24292e;
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

picture > img {
  vertical-align: bottom;
}

.container {
  max-width: 960px;
  padding: 0 60px;
  margin: 0 auto;
}

.p-index__mv {
  height: 420px;
  margin-bottom: 87px;
}
.p-index__mv img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.p-index__works {
  margin-bottom: 137px;
}
.p-index__news {
  margin-bottom: 205px;
}
.p-index__link {
  text-align: center;
  margin-top: 54px;
}
.p-index__link > a {
  color: #24292e;
}
.p-index__link > a:hover {
  text-decoration: none;
}