.v-header {
  padding: 121px 0;
}
.v-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.v-header__logo {
  margin: 0;
}
.v-header__nav > ul {
  display: flex;
  justify-content: flex-end;
  padding: 0;
  margin: 0;
  list-style: none;
}
.v-header__nav > ul > li {
  margin-left: 12px;
}
.v-header__nav > ul > li > a {
  display: inline-block;
  padding: 4px 10px;
  color: #24292E;
  font-size: 1.44rem;
  text-decoration: none;
}